import {Component, OnInit} from '@angular/core';
import {ApiService} from '../shared/api.service';
import {ActivatedRoute, Route} from '@angular/router';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'app-my-account',
  templateUrl: './my-account.component.html',
  styleUrls: ['./my-account.component.css']
})
export class MyAccountComponent implements OnInit {
  activeTab: any = 1;
  public allQuestions: any;
  public myQuestion: any;
  public doctor_id: any;
  public showAlert: boolean = false;
  _alert: any = {};
  //vote
  public voteInfo: any;
  public voteAnswer: any;
  isActive: number = 5;
  public waiting: any;
  public sourceImage: any;

  constructor(public api: ApiService, public router: ActivatedRoute, public sanitizer:DomSanitizer) {
    // this.router.queryParamMap.subscribe(params => {
    //   this.activeTab = params.get('voting');
    // });
  }

  ngOnInit() {
    this.loadAllQuestions();
    let userInfo = JSON.parse(localStorage.getItem('userInfo'));
    this.doctor_id = userInfo.doctor_id;

    setInterval(() => {
      this.loadAllQuestions();
    }, 300000);
  }

  loadAllQuestions() {
    this.api.getAllQuestions().subscribe((res) => {
      this.allQuestions = res.data;
    });
  }

  sendQuestion(data) {
    if (data.invalid) {
      this._alert = {
        message: 'Invalid username or password.',
        title: 'Failed!',
        success: false
      };
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false;
      }, 3000);
      return;
    }
    let _question = {doctor_id: this.doctor_id, name: data.value.name};

    this.api.setQuestion(_question).subscribe((res) => {
      this.loadAllQuestions();
      this._alert = {
        message: 'Successfully Registered',
        title: 'Success!',
        success: true
      };
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false;
      }, 3000);
      data.reset();
    });
  }

  getCurrentVote() {
    this.api.getVote().subscribe((res) => {
      this.voteInfo = res.data;
      this.checkVote();

    });
  }

  myVote() {
    if (this.voteAnswer) {
      let voteObj = {answer_id: this.voteAnswer, question_id: this.voteInfo.id, doctor_id: this.doctor_id};
      this.api.setVote(voteObj).subscribe((res) => {
        // this.voteInfo = res.data;
        this.waiting = 'Found';
        this._alert = {
          message: 'Successfully Registered',
          title: 'Success!',
          success: true
        };
        this.showAlert = true;
        setTimeout(() => {
          this.showAlert = false;
        }, 3000);
      });
    }
  }

  checkVote() {
    if (this.voteInfo && this.voteInfo.id) {
      this.api.getCheckVote({question_id: this.voteInfo.id, doctor_id: this.doctor_id}).subscribe((res) => {
        this.waiting = res.success;
      });
    } else {
      this.waiting = null;
    }

  }

  getQr() {
    let myQr = JSON.parse(localStorage.getItem('userInfo'));
    this.api.getMyQr(myQr.doctor_id).subscribe((res) => {
      this.sourceImage = this.sanitizer.bypassSecurityTrustResourceUrl('data:Image/*;base64,'+res.data);
    });
  }
}
