import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {ConfigService} from './config.service';
import {JwtHelperService} from '@auth0/angular-jwt';

let httpOptions;

@Injectable()
export class ApiService {
  url = this.config.baseURL() + this.config.apiURL() + this.config.EventId();
  public _token: any;
  public jwtHelper = new JwtHelperService();
  public imagePath: string;

  constructor(private http: HttpClient, private config: ConfigService) {
  }

  setHeader() {
    this._token = '' || localStorage.getItem('token');
    httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + this._token
      })
    };
    return httpOptions;
  }

  loggedIn() {
    let token = localStorage.getItem('token');
    if (token) {
      let isExp = this.jwtHelper.isTokenExpired(token);
      if (isExp) {

        localStorage.removeItem('token');
        localStorage.removeItem('userInfo');

      }
      return isExp;
    } else {
      return true;
    }
  }

  signUp(obj): Observable<any> {
    return this.http.post(this.config.baseURL() + this.config.apiURL() + 'register', obj).pipe(map(res => {
      return res;
    }));
  }
  registerDoctor(obj): Observable<any> {
    return this.http.post(this.config.baseURL() + this.config.apiURL() + '1/Doctor/ceateDocLanding', obj).pipe(map(res => {
      return res;
    }));
  }

  login(obj, eventId): Observable<any> {
    return this.http.post<any>(this.config.baseURL() + this.config.apiURL() + eventId + '/' + 'login', obj).pipe(map(res => {
      return res;
    }));
  }

  getGallery(): Observable<any> {
    return this.http.get(this.config.baseURL() + this.config.apiURL() + 'getAllGallery').pipe(map(res => {
      return res;
    }));
  }

  getSchedule(): Observable<any> {
    return this.http.get('../../assets/schedule.json').pipe(map(
      res => {
        return res;
      }
    ));
  }

  getSpeakers(): Observable<any> {
    return this.http.get('../../assets/speakers.json').pipe(map(
      res => {
        return res;
      }
    ));
  }

  getAllQuestions(): Observable<any> {
    return this.http.get(this.config.baseURL() + this.config.apiURL() + '1/Question/getAllQuestion', this.setHeader()).pipe(map(res => {
      return res;
    }));
  }

  setQuestion(body): Observable<any> {
    return this.http.post(this.config.baseURL() + this.config.apiURL() + '1/Question/setQuestion', body, this.setHeader()).pipe(map(res => {
      return res;
    }));
  }

  getVote(): Observable<any> {
    return this.http.get(this.config.baseURL() + this.config.apiURL() + '1/Vote/getQuestionSendToVote', this.setHeader()).pipe(map(res => {
      return res;
    }));
  }

  setVote(body): Observable<any> {
    return this.http.post(this.config.baseURL() + this.config.apiURL() + '1/Vote/setVote', body, this.setHeader()).pipe(map(res => {
      return res;
    }));
  }

  getCheckVote(body): Observable<any> {
    return this.http.post(this.config.baseURL() + this.config.apiURL() + '1/Vote/checkDoctor', body, this.setHeader()).pipe(map(res => {
      return res;
    }));
  }

  getMyQr(id): Observable<any> {
    return this.http.get(this.config.baseURL() + this.config.apiURL() + 'qrCode/'+id, this.setHeader()).pipe(map(res => {
      return res;
    }));
  }

}


