import {Component, ElementRef, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import {ApiService} from '../shared/api.service';
declare let $: any;

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
  public allImages: any[] = [];
  public config: any;
  constructor(public callApi:ApiService) {
  }

  ngOnInit() {
    this.callApi.getGallery().subscribe((res)=>{
      this.allImages = res.data;
    });
  this.config = {
    dots: false,
    infinite: true,
    speed: 500,

    slidesToShow: 1,
    slidesToScroll: 1,


    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }

    ]

  }

  }


}
